html {
  scroll-behavior: smooth;
}

.backgroundWhite {
  padding-top: 1em;
}

/* tablets */
@media only screen and (min-width: 600px) {
  .backgroundWhite {
    padding-top: 3em;
  }
}

/* desktops */
@media only screen and (min-width: 768px) {
  .backgroundWhite {
    padding-top: 2em;
  }
}

@media only screen and (min-width: 960px) {
  .backgroundWhite {
    padding: 5em;
  }
}

@media only screen and (min-width: 1300px) {
  .backgroundWhite {
    padding: 8em 10em;
  }
}
