.social-logos {
  display: none;
}

.social-logos>a {
  margin: .2em 0;
}

.socialIconn {
  transition: 0.4s;
}

.socialIconn:hover {
  cursor: pointer;
  transition: 0.4s;
  transform: scale(1.2);
}

.poster {
  display: flex;
  padding: 1em 2em;
}

.poster>a>img {
  width: 50px;
  height: 50px;
}

@media only screen and (min-width: 600px) {}

@media only screen and (min-width: 768px) {}

@media only screen and (min-width: 960px) {
  .social-logos {
    display: block;
  }

  .socialIconn {
    font-size: xx-large;
    color: white;
    transition: 0.4s;
  }

  .poster {
    display: flex;
    justify-content: space-between;
    padding: 1em 6em;
  }


}

@media only screen and (min-width: 1300px) {}